import Vue from 'vue';
import { Button, Input, Checkbox } from 'ant-design-vue';

import App from './App.vue';
import router from './router';
import store from './store';

Vue.use(Button);
Vue.use(Input);
Vue.use(Checkbox);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
